.accordion-accordion {
    display: flex;
    position: relative;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
  }
   
  .accordion-element1 {
    width: 640px;
    user-select: none;
  }
   
  .accordion-details1 {
    gap: var(--dl-space-space-halfunit);
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
  }
   
  .accordion-text1 {
    color: var(--dl-color-gray-white);
    width: 100%;
    font-size: 18px;
    max-width: 535px;
    font-style: normal;
    font-family: Poppins;
    font-weight: 600;
    line-height: 27px;
  }
   
  .accordion-text2 {
    color: var(--dl-color-gray-900);
    width: 100%;
    max-width: 535px;
    font-family: Poppins;
    line-height: 24px;
    user-select: text;
  }
   
  .accordion-icon1 {
    fill: var(--dl-color-gray-white);
    width: var(--dl-size-size-xsmall);
    height: var(--dl-size-size-xsmall);
  }
   
  .accordion-element2 {
    width: 640px;
    user-select: none;
  }
   
  .accordion-details2 {
    gap: var(--dl-space-space-halfunit);
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
  }
   
  .accordion-text3 {
    color: var(--dl-color-gray-white);
    width: 100%;
    font-size: 18px;
    max-width: 535px;
