    line-height: 27px;
  }
   
  .accordion-text4 {
    color: var(--dl-color-gray-900);
    width: 100%;
    max-width: 535px;
    font-family: Poppins;
    line-height: 24px;
    user-select: text;
  }
   
  .accordion-icon3 {
    fill: var(--dl-color-gray-white);
    width: var(--dl-size-size-xsmall);
    height: var(--dl-size-size-xsmall);
  }
   
  .accordion-element3 {
    width: 640px;
