  @media(max-width: 767px) {
    .accordion-element1 {
      width: 100%;
      max-width: 560px;
    }
    .accordion-text1 {
      font-size: 16px;
      line-height: 24px;
    }
    .accordion-element2 {
      width: 100%;
      max-width: 560px;
    }
    .accordion-text3 {
      font-size: 16px;
    }
    .accordion-element3 {
      width: 100%;
      max-width: 560px;
    }
    .accordion-text5 {
      font-size: 16px;
    }
  }
   
  @media(max-width: 479px) {
    .accordion-element1 {
      width: 350px;
      max-width: auto;
    }
    .accordion-element2 {
      width: 350px;
      max-width: auto;
    }
    .accordion-element3 {
