.faq-accordion {
    width: 100%;
    display: flex;
    position: relative;
    align-items: center;
    flex-direction: column;
    justify-content: center;
  }
   
  .faq-element1 {
    width: 100%;
    max-width: 800px;
    padding-top: var(--dl-space-space-threeunits);
    padding-bottom: var(--dl-space-space-threeunits);
  }
   
  .faq-details1 {
    gap: var(--dl-space-space-oneandhalfunits);
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
  }
   
  .faq-text10 {
    color: rgb(255, 255, 255);
    width: 100%;
    font-size: 20px;
    font-style: normal;
    font-family: Poppins;
    font-weight: 500;
    line-height: 28px;
  }
   
  .faq-text11 {
    color: rgb(204, 204, 204);
    width: 100%;
    font-family: Poppins;
    line-height: 24px;
    user-select: text;
  }
   
  .faq-icon10 {
    fill: var(--dl-color-gray-white);
